import { toURLSearchParams } from '@wix/sdk-runtime/rest-modules';
import { transformRESTTimestampToSDKTimestamp } from '@wix/sdk-runtime/transformations/timestamp';
import { transformPaths } from '@wix/sdk-runtime/transformations/transform-paths';
import { resolveUrl } from '@wix/sdk-runtime/rest-modules';
import { ResolveUrlOpts } from '@wix/sdk-runtime/rest-modules';
import { RequestOptionsFactory } from '@wix/sdk-types';

function resolveWixBlogV1BlogCacheServiceUrl(
  opts: Omit<ResolveUrlOpts, 'domainToMappings'>
) {
  const domainToMappings = {
    'bo._base_domain_': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'wixbo.ai': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'wix-bo.com': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'manage._base_domain_': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'apps._base_domain_': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'editor._base_domain_': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'blocks._base_domain_': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'create.editorx': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    _: [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'www._base_domain_': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    '*.dev.wix-code.com': [
      {
        srcPath: '/_api/blog-cache-service',
        destPath: '',
      },
    ],
    'api._api_base_domain_': [
      {
        srcPath: '/blog-cache-service',
        destPath: '',
      },
    ],
  };

  return resolveUrl(Object.assign(opts, { domainToMappings }));
}

const PACKAGE_NAME = '@wix/auto_sdk_blog_blog-cache';

/** Gets blog cache. */
export function getBlogCache(payload: object): RequestOptionsFactory<any> {
  function __getBlogCache({ host }: any) {
    const metadata = {
      entityFqdn: 'wix.blog.v1.blog_cache',
      method: 'GET' as any,
      methodFqn: 'wix.blog.v1.BlogCacheService.GetBlogCache',
      packageName: PACKAGE_NAME,
      url: resolveWixBlogV1BlogCacheServiceUrl({
        protoPath: '/v1/blog-cache',
        data: payload,
        host,
      }),
      params: toURLSearchParams(payload),
      transformResponse: (payload: any) =>
        transformPaths(payload, [
          {
            transformFn: transformRESTTimestampToSDKTimestamp,
            paths: [{ path: 'blogCache.updatedDate' }],
          },
        ]),
    };

    return metadata;
  }

  return __getBlogCache;
}
